define('ember-esri/components/esri-legend', ['exports', 'ember', 'esri/dijit/Legend'], function (exports, Ember, Legend) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({

    classNames: ['legendDiv'],
    willRemoveElement: function willRemoveElement() {
      var legend = this.get('legend');
      if (legend) {
        legend.destroy();
      }
    },
    onMapChange: (function () {
      var legend = this.get('legend');
      var map = this.get('map');
      if (map && legend) {
        legend.set('map', map);
        legend.refresh();
      }
      if (map && !legend) {
        legend = new Legend['default']({
          map: map
        }, this.elementId);
        this.set('legend', legend);
        legend.startup();
      }
    }).observes('map')

  });

});