define('ember-esri/components/esri-search', ['exports', 'ember', 'esri/dijit/Search'], function (exports, Ember, Search) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({

    willRemoveElement: function willRemoveElement() {
      var search = this.get('search');
      if (search) {
        search.destroy();
      }
    },

    onMapChange: (function () {
      var search = this.get('search');
      var map = this.get('map');
      if (map && search) {
        search.set('map', map);
      }
      if (map && !search) {
        search = new Search['default']({
          map: this.get('map')
        }, this.elementId);
        this.set('search', search);
      }
    }).observes('map')

  });

});