/* jshint ignore:start */

define('ember-esri/config/environment', ['ember'], function(Ember) {
  var prefix = 'ember-esri';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("ember-esri/tests/test-helper");
} else {
  require("ember-esri/app")["default"].create({"name":"ember-esri","version":"0.0.0.5a9a1ab2"});
}

/* jshint ignore:end */
